import React from "react";
import styled from "styled-components";

const GlassLine: React.FC = () => {
    return (
        <LineWrapper />
    );
}

export default GlassLine;

const LineWrapper = styled.div`

width: 96%;
height: 2px;
margin-left: 2%;
margin-right: 2%;
background: linear-gradient(225deg,rgba(165, 229, 255, 0.451) 20%,rgba(35, 128, 156, 0.2) 100%);
box-shadow: 5px 5px 10px  rgba(11, 20, 20, 0.5);

`