import React, { useState } from "react";
import styled from 'styled-components';
import { GlassFooter, GlassBody, GlassNav } from "./sub-components";
import Products from "../product/products";
import { useGlobalStateContext } from "../../../context/ui-context";

const MainGlass: React.FC = () => {

  const { isOpen }: any = useGlobalStateContext();

  return (
    <BodyWrapper flow={(isOpen === true) ? 100 : 0}>
      <div className="main-body">
        <Products isOpen={isOpen} />
        <div className="glass-body">
          <GlassNav />
          <GlassBody />
          <div className="glass-footer">
            <GlassFooter />
          </div>
        </div>
      </div>
    </BodyWrapper>
  );
}

export default MainGlass;

/****************************************************************************************************
 * GLASS STYLES 
****************************************************************************************************/
interface Props {
  flow: number
}

const BodyWrapper = styled.div<Props>`

.main-body{
  position: relative;
  display: flex;
  align-items: center;
  width: 94vw;
  height: 100vh;
  margin-left: 6vw;
  overflow-x: hidden;
  overflow-y: hidden
}

.glass-body{
  position: absolute;
  transform: translateX(${props => props.flow}vw);
  transition: 1.5s ease;
  background: linear-gradient(
    251deg,
    #a5e5ff73 -95%,
    rgba(15, 15, 15, 0.2) 65%
  );

  border-radius: 2px;
  margin-left: 2vh;
  margin-right: 2vh;
  height: 97%;
  width: 98%;
  box-shadow: 0 0 5vw rgba(0, 0, 0, 1), 0 0 0 0.5vw rgba(152, 236, 255, 0.5);
}

.glass-footer{
  @media (max-width: 980px), (max-height: 580px) {
    visibility: hidden;
  }
}

`