import { createContext, useContext, useReducer } from "react";

const GlobalDispatchState = createContext<React.DispatchWithoutAction | null>(null);
const GlobalUIState = createContext<StateProps | unknown>(null);

interface StateProps {
    isOpen: boolean,
    language: string | null
}

interface ActionProps {
    type: string
    payload: string | boolean
}

const reducer = (state: StateProps, action: ActionProps) => {

    switch (action.type) {
        case 'LANG_TYPE': {
            return { ...state, language: action.payload }
        }
        case 'OPEN_TYPE': {
            return { ...state, isOpen: action.payload }
        }
        default: {
            throw new Error(`Unhandled Type: ${action.type}`)
        }
    }
}

export const GlobalUserInterface: React.FC = ({ children }) => {

    const initialState: StateProps = {
        isOpen: false,
        language: window.localStorage.getItem('lang') == null ? 'pt-Br' : window.localStorage.getItem('lang')
    }

    const [state, dispatch] = useReducer<any>(reducer, initialState)

    return (
        <GlobalDispatchState.Provider value={dispatch}>
            <GlobalUIState.Provider value={state}>
                {children}
            </GlobalUIState.Provider>
        </GlobalDispatchState.Provider>
    );
}


export const useGlobalDispatchContext = () => useContext(GlobalDispatchState);
export const useGlobalStateContext = () => useContext(GlobalUIState)