import React from "react";
import styled from "styled-components";

const GlassBody: React.FC = () => {

    return (
        <BodyWrapper>
            <div className="first-block">
                <img src="./mock.png" alt="" />
            </div>
            <div className="second-block">
                <img src="./dummy.png" alt="" />
            </div>
        </BodyWrapper>
    )
}

export default GlassBody;

const BodyWrapper = styled.div`

width: 100%;
height:80vh;
display: flex;
flex-wrap: wrap;

.first-block{
    
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48%;
    min-width: 400px;
    backdrop-filter: blur(30px);
    margin-left: 2%;
    img{
        height: 60vh;
    }
}

.second-block{
    flex-grow: 1;
    overflow-y: scroll;
    overflow: auto;
    display: flex;
    flex-direction: column;
    width: 48%;
    height: 100%;
    background: white;
    margin-right: 2%;
    img{
        height: 60vh;
    }
}


`