import React, { useRef, useState } from "react";
import styled from "styled-components";
import { AiOutlineSearch, AiOutlineClose } from "react-icons/ai"

const GlassSearch: React.FC = () => {

  const [isActive, setIsActive] = useState<boolean>(false);
  const inputRef = useRef<any>()

  const _toggleSearch = () => {
    if (isActive) {
      inputRef.current.value = "";
    }
    setIsActive(!isActive)

  }

  return (
    <SearchWrapper isSearching={isActive}>
      <IconWrapper onClick={_toggleSearch}>
        {isActive ? (<AiOutlineClose size={18} />) : (<AiOutlineSearch size={22} />)}
      </IconWrapper>
      <InputWrapper type="text"
        ref={inputRef}
        placeholder=" Search..." />
    </SearchWrapper>
  );
}

export default GlassSearch;

interface Props {
  isSearching: boolean | any
}

const SearchWrapper = styled.div<Props>`
  width: ${props => props.isSearching ? 18 : 4}vh;
  height: 4vh;
  background: #f2f2f2;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all 0.3s ease;
  padding-left: 0.5vh;
`

const InputWrapper = styled.input`
    border: none;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: transparent;
    outline: none;
    font-size: 2vh;
    border: 1px solid transparent;
    &:focus {
      border-color: rgba(0,0,0,0.3);
    }
`

const IconWrapper = styled.button`
  border: none;
  height: 100%;
  cursor: pointer;
  background: none;
  transition: 0.2s;
  &:hover{
    color: white;
  }
`