import { motion, useMotionValue, useTransform } from "framer-motion";
import React from "react";
import styled from "styled-components";

import LogoImg from "../../../../assets/logo.png"

interface Props {
  item: { id: number }
}

const Card: React.FC<Props> = ({ item }) => {
  return (
    <CardWrapper>
      <CardItem />
    </CardWrapper>)
}

export default Card;

const CardWrapper = styled.div`
    margin-top: 5vh;
    margin-left: 1.5vw;
    perspective: 2000;
`
////////////////////////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////////////////////////
export function CardItem(props: any) {

  const x = useMotionValue(0)
  const y = useMotionValue(0)

  const rotateX = useTransform(y, [-100, 100], [30, -30]);
  const rotateY = useTransform(x, [-100, 100], [-30, 30]);

  return (
    <CardContainer
      style={{ x, y, rotateX, rotateY, z: 100 }}
      drag
      dragElastic={0.05}
      dragConstraints={{ top: 0, left: 0, right: 0, bottom: 0 }}
      whileTap={{ cursor: "grabbing" }}
    >
      <TopContainer>
        <ImageWrapper
        >
          <ProductImage /* style={{ rotate: "-5deg" }} */
            style={{
              x,
              y,
              rotateX,
              rotateY,
              z: 100
            }}
            drag
            dragConstraints={{ top: 0, left: 0, right: 0, bottom: 0 }}
            dragElastic={0.1}
            whileTap={{ cursor: "grabbing" }}
          >
            <img src="./mock.png" alt="" />
          </ProductImage>
        </ImageWrapper>
      </TopContainer>
      <BottomContainer>
        <ProductDetails />
        <LogoDetails />
      </BottomContainer>
    </CardContainer>
  )
}

const CardContainer = styled(motion.div)`
    width: 285px;
    height: 500px;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    box-shadow: 0 2px 7px 1px rgba(31, 31, 31, 0.2);
    background: linear-gradient(35deg, #ffffff 50%, #a5e5ff21 100%);
    color: #fff;
    position: relative;
    cursor: grab;
    /* overflow: hidden; */
`

const TopContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  flex: 1.2;
  position: relative;
  align-items: center;
  justify-content: flex-end;
`;

const BottomContainer = styled.div`
  display: flex;
  flex-direction: column;
  flex: 0.8;
  color: #ffffff;
  background: #1d1f21;
  border-radius: 10px;
`;

const ImageWrapper = styled(motion.div)`
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const ProductImage = styled(motion.div)`
  width: auto;
  height: 25vh;
  z-index: 99;
  user-select: none;
  margin-right: 0.2em;
  margin-top: 0.2em;

  img {
    width: auto;
    height: 100%;
    user-select: none;
  }
`;
////////////////////////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////////////////////////
export function ProductDetails(props: any) {
  return (
    <DetailsContainer>
      <SmallText>Fechadura de vidro</SmallText>
    </DetailsContainer>
  );
}

export function LogoDetails(props: any) {
  return (
    <Logo>
      <img src={LogoImg} />
    </Logo>
  )
}

const DetailsContainer = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  line-height: 1.4;
  padding: 10px;
`;

const SmallText = styled.span`
  font-size: 1.5vh;
  margin-top: 10px;
  color: white;
  text-transform: uppercase;
`;

const Logo = styled.div`
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
  img {
    width: auto;
    height: 1vh;
  }
`;