import { useEffect, useRef } from 'react';
import styled from 'styled-components';
import { useGlobalDispatchContext, useGlobalStateContext } from '../context/ui-context';
import Button from './button';

interface Props {
    isOpen: boolean;
}

const Navbar: React.FC = () => {

    const langRef: any = useRef();
    const state: any = useGlobalStateContext();
    const dispatch: any = useGlobalDispatchContext();

    useEffect(() => {
        langRef.current.value = window.localStorage.getItem('lang')
    }, [])

    useEffect(() => {
        window.localStorage.setItem('lang', state.language)
    }, [state.language])

    return (
        <NavWrapper isOpen={state.isOpen}>
            <div className="rotate">
                <div className="nav-bar">
                    <select
                        className="nav-select"
                        ref={langRef}
                        onChange={(e) => dispatch(({ type: 'LANG_TYPE', payload: e.target.value }))}>
                        <option value={"pt-Br"} > pt-Br</option>
                        <option value={"es"}> es</option>
                    </select>
                    <p>
                        Configurador da linha glass
                    </p>
                    <Button isOpen={state.isOpen} />
                </div>
            </div>
            <div className="nav-bar-product">
                <div className="header">

                </div>
                <div className="body">

                </div>
            </div>
        </NavWrapper>)
}

export default Navbar;

/****************************************************************************************************
 * NAVBAR STYLES 
****************************************************************************************************/
const NavWrapper = styled.div<Props>`

.rotate{
    position: fixed;
    width: 100vh;
    transform-origin: left top;
    transform: rotate(-90deg) translateX(-100%);
    z-index: 1;
}
.nav-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 100%;
    height: 5.5vw;
    color:rgb(255, 255, 255);
    text-shadow: 3px 3px rgba(7, 9, 10, 0.349);
    font-weight: 500;
    font-size: 2.5vh;
    text-transform: uppercase;
    background: linear-gradient( 251deg,#a5e5ff73 -95%,rgba(15, 15, 15, 0.2) 65%);
    box-shadow: 0 0 5vw rgba(0, 0, 0, 1), 0 0 0 0.5vw rgba(152, 236, 255, 0.5);
    animation: translateIn 0.5s ease;
    transform: translateY(${props => (props.isOpen) ? -100 : 0}%);
    transition: 1s;
}

.nav-bar-product {
    position: absolute;
    width: 6vw;
    height: 90vh;
    display: flex;
    justify-content: end;
    opacity: ${props => props.isOpen ? 1 : 0};
    transition: 1s;
    .header{
        display: flex;
        align-items: center;
        justify-content: center;
        height: 9.9vh;
        width: 100%;
        border-bottom: 1px solid white;
    }
    .body{
        height: 100%;
        background: ${props => (props.isOpen) ? "transparent" : "transparent"};
        transition: 1s;
        /* border-top: ${props => (props.isOpen) ? "1px solid white" : "none"}; */
    }
}

.nav-select{
    margin-left: 2vw;
    margin-right: 2vw;
    width: 70px;
    height: 20px;
    border-radius: 5px;
    background: rgb(255, 255, 255);
    color: #050505;
}

@keyframes translateIn {
    from{
        transform: translateY(-150%);
    }
    to{
        transform: translateY(0%);
    }
}
`