import React, { useState } from "react";
import styled from "styled-components";
import Button from "../../button";
import Card from "./sub-components/card";

interface Props {
    isOpen: boolean
}

interface StyledProps {
    isOpen: boolean,
    isChecked: boolean
}

const Products: React.FC<Props> = ({ isOpen }) => {

    const [checked, setChecked] = useState(false)
    let myList = [
        { id: 1 }, { id: 2 }, { id: 3 },
        { id: 4 }, { id: 5 }, { id: 6 },
        { id: 7 }, { id: 8 }, { id: 9 },
        { id: 10 }, { id: 11 }, { id: 12 },
        { id: 13 }, { id: 14 }, { id: 15 },
        { id: 16 }, { id: 17 }, { id: 18 },
        { id: 19 }, { id: 20 }, { id: 21 },
        { id: 22 }, { id: 23 }, { id: 24 },
        { id: 25 }, { id: 26 }, { id: 27 },
        { id: 28 }, { id: 29 }, { id: 30 },
        { id: 31 }, { id: 32 }
    ]

    const handleClick = () => {
        setChecked(!checked);
    }

    const handleChange = () => {
        console.log("A CHANGE!")
    }

    return (
        <ProductsWrapper isOpen={isOpen} isChecked={checked}>
            <div className="product-header">
                <img src="./logo.png" alt="PADO" />
                <div className="search-header">
                    <input
                        id="filters"
                        type="radio"
                        name="radio"
                        checked={checked}
                        onClick={handleClick}
                        onChange={handleChange}
                    >
                    </input>
                    <label htmlFor="filters">&nbsp;FILTROS</label>
                </div>
                <Button isOpen={isOpen} />
            </div>
            <div className="filter-section">

            </div>
            <div className="product-section">
                {myList.map(item => <Card key={item.id} item={item} />)}
                <div className="product-footer">
                    <img src="/footer.png" alt="" />
                </div>
            </div>
            <div className="end-section">

            </div>
        </ProductsWrapper>
    )
}

export default Products;

/****************************************************************************************************
 * PRODUCTS STYLES 
****************************************************************************************************/
const ProductsWrapper = styled.div<StyledProps>`
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    transition: 1s;
    opacity: ${props => (props.isOpen === true) ? 1 : 0};
    transition: all 1s;
    .product-header{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        height: 9.9vh;
        border-bottom: 0.5px solid whitesmoke;
        button{
            padding-right: 7.5vw;
        }
        img{
            height: 2vh;
            margin: 4vh;
        }
        .search-header{
            input{
                visibility:hidden;
            }   
            label{
                color: white;
            }
        }
    }
    .filter-section{
        min-width: ${props => (props.isChecked) ? 200 : 0}px;
        height: 90vh;
        background: ${props => (props.isChecked) ? "#303131;" : "transparent"}%;
        transition: 1s;
        flex-grow: ${props => (props.isChecked) ? 1 : 0};
        width: ${props => (props.isChecked) ? 25 : 1}%;
        /* border-left: 1px solid white; */
    }
    .product-section{
        width: 65%;
        flex-grow: 3;
        overflow-y: scroll;
        overflow: auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        height: 90vh;
        /* background: white; */
        padding-right: ${props => (props.isChecked) ? 8 : 0}%;
        transition: 1s;
        @media (min-width: 980px), (min-height: 580px) {
            padding-right:  ${props => (props.isChecked) ? 2 : 8}%;
        }

        .product-footer{
            margin-top: 5vh;
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            bottom: 0;
            height: 50vh;
            width: 100%;
            img{
                width: 99vw;
            }
        }
    }
    .end-section{
        width: ${props => (props.isChecked) ? 0 : 8}%;
        background: white;
        transition: 1s;
        @media (min-width: 980px), (min-height: 580px) {
         width: 0;
        }
    }

`