import React from "react";
import styled from "styled-components";
import GlassLine from "./glassLine";
import GlassSearch from "./glassSearch";


const GlassFooter: React.FC = () => {

    return (
        <BodyWrapper>
            <GlassLine />
            <div className="footer">
                <p className="text-capitalize">
                    &copy; PADOTec {new Date().getFullYear()}. All
                    Rights Reserved
                </p>
                <GlassSearch />
            </div>
        </BodyWrapper>
    )
}

export default GlassFooter;

const BodyWrapper = styled.div`

width: 100%;
height:7vh;

.footer{
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-top: 1vh;
    margin-left: 4vh;
    margin-right: 4vh;
    color: white;
    
}

`