import React from "react";
import styled from "styled-components";
import { useGlobalDispatchContext } from "../context/ui-context";

interface Props {
    isOpen: boolean
}

const Button: React.FC<Props> = ({ isOpen }) => {

    const dispatch: any = useGlobalDispatchContext();

    return (
        <ButtonWrapper onClick={(e) => dispatch({ type: 'OPEN_TYPE', payload: !isOpen })}>
            <div className="btn-div"></div>
            <div className="btn-div"></div>
        </ButtonWrapper>
    )
}

export default Button;

const ButtonWrapper = styled.button`
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin-right: 2vw ;
  top: 1.5vw;
  left: 1.5vw;
  width: 2.5vw;
  height: 2.5vw;
  z-index: 99;
  background: none;
  border: none;
  .btn-div{
      width: 4vh;
      height: 2px;
      background: white;
  }
  @media (max-width: 800px){
    width: 5.5vh;
    height: 3.5vw;
  }
`