import React from "react";
import styled from "styled-components";
import GlassLine from "./glassLine";

const GlassNav: React.FC = () => {

    return (
        <GlassNavWarapper>
            <nav className="glass-nav">
                <div className="first-block">
                    <div className="nav-link">
                        <img src="./pado.png" ></img>
                    </div>
                </div>
                <div className="second-block">

                </div>
            </nav>
            <GlassLine />
        </GlassNavWarapper>
    )
}

export default GlassNav;

const GlassNavWarapper = styled.div`

.glass-nav{
    display: flex;
    flex-wrap: wrap;
    width: 96%;
    height: 10vh;
    margin-left: 2%;
    margin-right: 2%;

    & .first-block{
        display: flex;
        justify-content: space-around;
        align-items: flex-end;
        width: 50%;
        height: 100%;
        color: whitesmoke;
        font-weight: 500;
        text-transform: uppercase;
        
        & .nav-link{
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: flex-start;
            align-items: flex-end;
            img{
                margin-left: 2vh;
                height: 6vh;
            }
        }
        & .nav-link :hover{
            cursor: pointer;
        }
    }

    & .second-block{
        position: relative;
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
        width: 50%;
        height: 100%;
    }
}
`